import styled from 'styled-components'

export const HeroContainer = styled.div`
    background: #0c0c0c;
    display: flex;
    margin-top: -175px;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 810px;
    position: relative;
    z-index: 1;

    :before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(180deg, rgba(0,0,0,0.2) 0% , rgba(0,0,0,0.6) 100%), 
        linear-gradient(180deg, rgba(0,0,0,0.2) 0% , transparent 100%);
        z-index: 2;
    }
`;
export const HeroBg = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
`;
export const VideoBg = styled.video`
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
    
`;
export const HeroContent = styled.div`
    z-index: 3;
    max-width: 1200px;
    margin-top: 200px;
    position: absolute;
    padding: 8px 25px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .Typewriter{
        margin-top: 24px;
        margin-bottom: 24px;
        color: #fff;
        font-size: 24px;
        text-align: center;
        max-width: 600px;

        @media screen and (max-width: 768px) {
            font-size: 24px;
        }

        @media screen and (max-width: 480px) {
            font-size: 18px;
        }
    }
    .Typewriter__cursor{
        color: transparent;
    }
`;
export const HeroH1 = styled.h1`
    color: #fff;
    font-size: 48px;
    text-align: center;

    @media screen and (max-width: 768px) {
        font-size: 40px;
    }

    @media screen and (max-width: 480px) {
        font-size: 32px;
    }
`;
export const HeroP = styled.p`
    margin-top: 24px;
    margin-bottom: 24px;
    color: #fff;
    font-size: 24px;
    text-align: center;
    max-width: 600px;

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 480px) {
        font-size: 18px;
    }
`;
export const SearchboxContainer = styled.div`
    width: 500px;
    height: 50px;
    position: relative;
    background-color: #fff;
    border-radius: 30px;
    display: flex;
    box-shadow: 0px 2px 12px 3px rgba(0,0,0,0.14);
    align-items: center;
    padding: 15px;
    z-index: 2;

    @media screen and (max-width: 768px) {
        width: 450px;
    }

    @media screen and (max-width: 480px) {
        width: 400px;
        height: 45px;
    }

    &.active{
        width: 60px;
        transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
`;
export const SearchboxIcon = styled.i`
    font-size: 30px;
    padding-top: 3px;
    color: #777;
    
    &:hover {
        transition: all 0.2s ease-in-out;
        color: #000;
        cursor: pointer;
    }

    @media screen and (max-width: 768px) {
        font-size: 25px;
    }
`;
export const Searchbox = styled.input`
    flex: 1;
    height: 40px;
    border: none;
    outline: none;
    font-size: 18px;
    padding: 0 10px 0 10px;
    color: #12112e;
    font-weight: 500;

    &:focus{
        outline: none;
        &::placeholder{
            opacity: 0;
        }
    }

    &::placeholder{
        color: #5B5F5D;
        transform: all 250ms ease-in-out;
    }
    @media screen and (max-width: 768px) {
        font-size: 15px;
    }
`;