import React from 'react';
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import DashboardIcon from '@material-ui/icons/Dashboard';
import PersonIcon from '@material-ui/icons/Person';
import SettingsIcon from '@material-ui/icons/Settings';
import PowerSettingsNewRoundedIcon from '@material-ui/icons/PowerSettingsNewRounded';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
    backgroundColor: '#000',
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  grow: {
    flexGrow: 1,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function ServiceProviderDBLayout(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider />
      <List>
            <Link className="linkColorRemoval" to="/admin">
                  <ListItem button>
                    <ListItemIcon>
                      <Link className="linkColorRemoval" to="/admin">
                        <DashboardIcon />
                      </Link>
                    </ListItemIcon>
                    <Link className="linkColorRemoval" to="/admin">
                      <ListItemText primary="Dashboard"/>
                    </Link>
                  </ListItem>
            </Link>
            <Link className="linkColorRemoval" to="/admin/profile">
                  <ListItem button>
                    <ListItemIcon>
                      <Link className="linkColorRemoval" to="/admin/profile">
                        <PersonIcon />
                      </Link>
                    </ListItemIcon>
                    <Link className="linkColorRemoval" to="/admin/profile">
                      <ListItemText primary="Profile"/>
                    </Link>
                  </ListItem>
            </Link>
            <Link className="linkColorRemoval" to="/admin/settings">
                  <ListItem button>
                    <ListItemIcon>
                      <Link className="linkColorRemoval" to="/admin/settings">
                        <SettingsIcon />
                      </Link>
                    </ListItemIcon>
                    <Link className="linkColorRemoval" to="/admin/settings">
                      <ListItemText primary="Settings"/>
                    </Link>
                  </ListItem>
            </Link>
            <Link className="linkColorRemoval" to="/logout">
              <ListItem button>
                <ListItemIcon>
                  <PowerSettingsNewRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="Log Out"/>
              </ListItem>
            </Link>
        </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Link to="/" className="linkColorRemoval">
            <Typography variant="h6" noWrap>
              Needsara
            </Typography>
          </Link>
          <div className={classes.grow} />
          <Link to="/" className="linkColorRemoval">
            <Typography variant="h6" noWrap>
              Home
            </Typography>
          </Link>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
}

ServiceProviderDBLayout.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ServiceProviderDBLayout;